import './navbar.css';
import left from '../assets/left.svg';
import right from '../assets/right.svg';
import { NavLink } from 'react-router-dom';
import { navData } from './navData';
import { useState } from 'react';

const Navbar = () => {
    const [open, setopen] = useState(true);
    const toggleOpen = () => {
        setopen(!open)
    }
    return (
    <div className={open?"mct__navbar":"mct__navbar_closed"}>
        <div className="mct__navbar-links_container">
            <ul>
                <li className='toggle'>
                    <button className='mct__navbar-collapse_button' onClick={toggleOpen}>
                        <img src={open ? left : right} alt={open ? "" : ""}></img>
                    </button>
                </li>
                {navData.map(item => {
                    return <li key={item.id} className='mct__navbar-item'>
                        <NavLink to={item.link} 
                        style={({isActive}) => !isActive ? {textDecoration: 'none', color: 'black'} : {color: 'black'}}
                        >
                            
                                <span className='inside'>
                                <ul>
                                <li className='img'> <img src={item.icon} alt={item.text}></img> </li>
                                
                                
                                    <li className='txt'> {item.text} </li>
                                </ul>
                                </span>
                            
                        </NavLink>
                        </li>
                })}
            </ul>
        </div>
    </div>
    )
}

export default Navbar;