import './shop.css'
import Product from './Product'
const Main = (props) => {
    const {products, add, remove} = props;
    return (
        <div className='mct__shop-main'>
            {products.map(product => {
                return (<Product 
                    add = {add}
                    remove = {remove}
                    key={product.id} 
                    product={product}
                    />)
            })}  
        </div>

    )
}

export default Main;