import './shop.css';
import {data} from './shopData.js';
import { useState } from 'react';
import Main from './Main'
import Cart from './Cart'

const Shop = () => {

    const [cart, setCart] = useState([]);

    const addProduct = (product, qty=1) => {
        const exist = cart.find((x) => x.id === product.id)
        if (exist) {
            const newCart = cart.map((x) => 
            x.id === product.id ? {...exist, qty: exist.qty + qty} : x)
            setCart(newCart)
        } else {
            const newCart = [...cart, {...product, qty: qty}];
            setCart(newCart)
        }
        // console.log(cart)
        if (product.qty===0)
        {
            product.qty = qty;
        }
        else {
            product.qty += qty;
        }
    }

    const removeProduct = (product, qty=1) => {
        const exist = cart.find((x) => x.id === product.id)
        if (exist.qty <= qty) {
            const newCart = cart.filter((x) => x.id !== product.id)
            setCart(newCart)
        } else {
            const newCart = cart.map((x) => 
            x.id === product.id ? {...exist, qty: exist.qty - qty} : x)
            setCart(newCart)
        }
       // console.log(cart)
       if (product.qty<=qty) {
            product.qty = 0;
       } else {
            product.qty -= qty;
       }
    }

    const removeAll = () => {
        setCart([])
        data.forEach((obj) => obj.qty=0)
    }

    return (
        <div>
            {/* <div className='mct__info'>{cart.length}  </div> */}
            <div className='mct__shop'>
            <Main cart={cart} products={data} add={addProduct} remove={removeProduct}/>
            <div className='mct__shop-cart'>
                <Cart cart={cart} remove={removeProduct} removeAll={removeAll}/>
            </div>
            </div>
        </div>
    )
}

export default Shop;