import { useContext } from 'react';
import { CalcContext } from './CalcContext';
import './calculator.css';
import { default as EqualHandler } from './inputParser';

const Display = () => {
    const {calc, setCalc} = useContext(CalcContext);
    // const input = document.querySelector("input");
    // input.addEventListener("keyup", (event) => {
    //     if (event.key === " ") {
    //         //event.preventDefault();
    //         equalHandler();
    //     }
    // });

    var mexp = require('math-expression-evaluator');
    const equalHandler = () => {
        let res = '';
        if (calc.lp === calc.rp) {
            try {
                res = mexp.eval(calc.inp)
            } catch (error) {
                res = ""
            }
        } else {
            res = " ()" 
            console.log(calc.rp + " " + calc.lp)
        }
        setCalc({
            ...calc,
            res: res,
        })
    }

    const handleSubmit = () => {
        console.log("submit");
    }

    return (
        <div className='mct__display'>
            <div className='mct__display-inp'>
                <form onSubmit={handleSubmit}>
                <input type="text" id="inp" size="20" value={calc.inp}
                    onChange={(e) => setCalc({...calc, inp: e.target.value})}
                    // onKeyDown={(e)=> e.key==="Enter" ? setCalc({...calc, res: 'E',}) : null } 
                    autoFocus={true} onBlur={({ target }) => target.focus()}
                >
                </input>
                </form>
            </div>
            <div className='mct__display-out'>
                {calc.res}
            </div>
        </div>
    )
}

export default Display;