import { useState } from "react";

const Product = (props) => {
    const {cart, product, add, remove} = props;
    const [qty, setQty] = useState(1);
    const handleChange = (event) => {
        const quantity = parseInt(event.target.value, 10);
        setQty(quantity);
        console.log(quantity);
    }
    return(
    <div className="mct__shop-item">
        <div className="title"><h3>{product.item}</h3></div>
        <div className="cover"><img src={product.cover} alt={product.item}></img></div>
        <div className="description">{product.author}</div>
        <input className="qty" type="number" placeholder="1" name="qty" onChange={e => handleChange(e)}></input>
        <div className="buttons"> 
            <button onClick={()=>add(product, qty)}></button>
            <button onClick={()=>remove(product, qty)} disabled={product.qty===0}></button>
        </div>
        <p className="cost">: {product.price}</p>
    </div>
    )
}

export default Product;