import './about.css';
import cat from '../assets/cat.jpg';
import {links} from './links';

const About = () => {
    return (
        <div className='mct__about'>
            <div className='mct__about-image'>
                <img src={cat} alt="White cat staring at the camera"></img>
            </div>
            <div className='mct__about-text'>
                <p key='name'>  .</p>
                <p key='basics'> 22               .</p>
                <p key='contact'>     !</p>
            </div>
            <div className='mct__about-links'>
                {links.map(lnk => {
                    return (
                        <a href={lnk.link}><img src={lnk.icon} alt={lnk.text} key={lnk.text}></img></a>
                    )
                })}
            </div>
        </div>
    )
}

export default About;