import './calculator.css';
import Display from './Display';
import Keyboard from './Keyboard';
import CalcProvider from './CalcContext';
const Calculator = () => {
    return(
        <CalcProvider>
            <div className='mct__calc'>
                <div className='mct__calc-screen'>
                    <Display />
                </div>
                <div className='mct__calc-keyboard'>
                    <Keyboard />
                </div>
            </div>
        </CalcProvider>
    )
}

export default Calculator;