import './calculator.css';
import Key from './Key';

const keyValues = [
    '0', '*', '/', '.',
    '7', '8', '9', '-',
    '4', '5', '6', '+',
    '1', '2', '3', 'AC',
    '(', ')', 'CE', '=',
]

const Keyboard = () => {
    return (
        <div className='mct__calc-keys'>
            {keyValues.map(key => <Key keyValue={key}/>)}
        </div>
    )
}

export default Keyboard;