import './quiz.css';

const Option = (props) => {
    const {option, answer, quiz, setQuiz} = props


    const handleOption = () => {
        if (option === answer) {
            setQuiz({
                ...quiz,
                question: quiz.question + 1,
                answered: true,
                result : quiz.result + 1,
            });
            console.log("correct")
        }
        else {
            setQuiz({
                ...quiz,
                question: quiz.question + 1,
                answered: true,
            })
        }
        if (quiz.question === quiz.size-1) {
            setQuiz({
                ...quiz,
                result: option === answer ?  quiz.result + 1 : quiz.result,
                finished: true,
                condition: false,
            })
        }
    }
    return (
        <div className='option'>
            <button onClick={handleOption}>
                {option}
            </button>
        </div>
    )
}

export default Option;