import './quiz.css';
import {quizData} from './quizData';
import left from '../assets/left.svg';
import right from '../assets/right.svg';
import { useState, useEffect } from 'react';
import Question from './Question';
import Option from './Option';

const Quiz = () => {
    const [quiz, setQuiz] = useState({
        started: false,
        question: 0,
        result: 0,
        answered: false,
        finished: false,
        size: quizData.length,
        condition: false,
    })
    const [time, setTime] = useState(0)

    useEffect(() => {
        let interval = null;
        if(quiz.condition) {
            interval = setInterval(()=> {
                setTime(prevTime => prevTime + 10)
            }, 10)
        } else {
            clearInterval(interval)
        }
        return () => clearInterval(interval);
    }, [quiz.condition]);

    const startQuiz = () => {
        setQuiz({
            ...quiz,
            started: true,
            question: 0,
            result: 0,
            condition: true,
            finished: false,
        });
        setTime(0);
    }

    return (
        <div className='quiz'>
        {!quiz.started ? 
            <div className='quiz__start'>
                <button className='quiz__start-button'
                        onClick={()=>startQuiz()}>
                    
                </button>
            </div>
        :
        <div>
            {!quiz.finished ?
                <div key="Quiz started">
                    <div className='stopwatch'>
                        <span>{("0" + Math.floor((time/60000)%60)).slice(-2)}:</span>
                        <span>{("0" + Math.floor((time/1000)%60)).slice(-2)}</span>
                    </div>
                    <Question question={quizData[quiz.question].quest} />
                    <div className='options'>
                        {quizData[quiz.question].opt.map(option => {
                            return(
                                <Option option={option} answer={quizData[quiz.question].ans} quiz={quiz} setQuiz={setQuiz}/>
                            )
                        })}
                    </div>
                </div> 
            :

                <div className="quiz__finish">
                    <h2>:</h2>
                    <p> : {quiz.result}/{quiz.size}</p> 
                    <p>: <span>{("0" + Math.floor((time/60000)%60)).slice(-2)}:</span>
                        <span>{("0" + Math.floor((time/1000)%60)).slice(-2)}</span></p>  
                    <button className='startover-button' onClick={()=>startQuiz()}></button>
                </div>
            }
        </div>
        } 
        </div>
    )



    // const [question, setQuestion] = useState(0);
    // const [result, setResult] = useState(false);
    // const [answer, setAnswer] = useState(false);
    // const [total, setTotal] = useState(0);

    // const nextQuestion = () => {
    //     setResult(false);
    //     setAnswer(false);
    //     setQuestion(question === quizData.length - 1 ? question : question+1);
    // }

    // const checkAnswer = (sumbitted) => {
    //     const res = sumbitted === quizData[question].ans ? true : false
    //     if (res) setTotal(total+1);
    //     setResult(res);
    //     setAnswer(true);
    // }

    // return (
    //     <div className='mct__quiz'>
    //     { !answer ?
    //         <div className='mct__quiz-display_question'>
    //             <div className='mct__quiz-question'>
    //                 {quizData[question].quest}
    //             </div>
    //             <div className='mct__quiz-options'>
    //                 {quizData[question].opt.map((item, ind) => {return <button onClick={() => checkAnswer(ind)}>{item}</button>})}
    //             </div>
    //         </div> :
    //             <div className='mct__results'>
    //                 {result ? <p>!</p> : <p>...</p>}
    //                 {question === quizData.length-1? 
    //                     <p>! : {total}/{quizData.length}</p> :
    //                     <button onClick={()=>nextQuestion()}>!</button>
    //                 }
    //             </div>
    //     }
    //     </div>
    // )
}

export default Quiz;