import { Outlet } from 'react-router-dom';
import './layout.css';
import Navbar from '../navbar/Navbar';
import Header from '../header/Header';

const Layout = () => {
    return (
        <div className='App'>
            <div className='Header'>
                <Header />
            </div>
            <div className='Main'>
            <Navbar />
            <div className='Page'>
                <Outlet />
            </div>
            </div>
        </div>
    )
}

export default Layout;