import slides from './slideData';
import left from '../assets/arrow-left.svg';
import right from '../assets/arrow-right.svg';
import play from '../assets/play.svg';
import pause from '../assets/pause.svg';
import settings from '../assets/slider.svg';
import './slideshow.css';
import { useState, useEffect } from 'react';
import Popup from 'reactjs-popup';
import 'reactjs-popup/dist/index.css';

const Slideshow = () => {
     const [slide, setSlide] = useState(0);
     const [show, setShow] = useState(false);
     const [delay, setDelay] = useState(2);

    // useEffect(() => {
    //     document.addEventListener('keydown', handleKeyDown, true);
    //     return () => {
    //         document.removeEventListener('keydown', handleKeyDown);
    //     };
    // }, [handleKeyDown]);

    // const handleKeyDown = (e) => {
    //     if (e.key === "right") {slideNext()}
    //     if (e.key === "left") {slidePrev()}
    //     if (e.key === " ") {startShow()}
    // }

   useEffect(() => {
        let timer = null;
        let delayms = delay * 1000;
        if (show) {
            timer = setInterval(()=>{setSlide(prevSlide => prevSlide === slides.length - 1 ? 0 : prevSlide + 1)}, delayms);
        } else {
            clearInterval(timer)
        }
        return () => clearInterval(timer);
    }, [show]) 
   


    const slideNext = () => {
        setSlide(slide === slides.length-1 ? 0 : slide+1)
    } 

    const slidePrev = () => {
        setSlide(slide === 0 ? slides.length-1 : slide-1)
    }

    const goToSlide = (index) => {
        setSlide(index);
    }

    const startShow = () => {
        console.log(show)
        show ? setShow(false) : setShow(true);
    }

    return (
        <div className='mct__slideshow-container'>
            <div className='mct__slideshow-top'>
                <div className='mct__menu-stats'>
                    {slide+1}/{slides.length}
                </div>
                <div className='mct__menu-anim'>
                    <button onClick={()=>startShow()}><img src={show ? pause : play} alt="Play"></img></button>
                    <Popup trigger={<button><img src={settings} alt="Settings"></img></button>}>
                        <div className='menu-speed'>
                             : <input 
                            type="number" min="1" size="5" placeholder={delay} 
                            onChange={(e) => setDelay(parseInt(e.target.value, 10))}
                            ></input> .
                        </div>
                    </Popup>
                </div>
            </div>

            <div className='mct___slideshow'>
                <div className='mct__slideshow-menu'>
                    <button id="left" onClick={slidePrev}><img src={left} alt=""/></button>
                </div>

                <div className='mct__slideshow-main'>
                    <div className='mct__slideshow-slide'>
                        <img src={slides[slide]} alt="Slide"></img>
                    </div>
                </div>

                <div className='mct__slideshow-menu'>
                    <button id="right" onClick={slideNext}><img src={right} alt=""/></button>
                </div>
            </div>
            <div className='mct__slideshow-bottom'>
                {slides.map((_slide, index) => (
                    <div key={index} className={index===slide ? "current-dot" : "dot"} onClick={() => goToSlide(index)}>
                        
                    </div>
                ))}
            </div>
        </div>
    )
}

export default Slideshow;